.cnt-container svg {
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none; }
  .cnt-container svg .cnt-selected {
    color: #0066ff;
    fill: currentColor; }
  .cnt-container svg .cnt-highlighted {
    color: #ff9900;
    fill: currentColor; }
  .cnt-container svg .cnt-selectable {
    pointer-events: bounding-box; }
  .cnt-container svg .cnt-selectable:hover {
    fill: #4d94ff; }
  .cnt-container svg rect.cnt-areaSel {
    stroke: gray;
    stroke-dasharray: 4px;
    stroke-opacity: 0.5;
    fill: #0066ff;
    fill-opacity: .3; }

.cnt-container .cnt-emabox {
